

:root {
    --header-height: 85vh;
    --height2: calc(var(--header-height) - max(8vw, 4rem) - 2vh);
    --block-height: min(calc(var(--height2)/2 - 2vw), 31.5vw);
    --block-width: calc(1.52*var(--block-height));
    --spacing: 1.5vw;
    --iconHeight: 10vw;
    --portfolio-body-width: min(90vw, 70rem, max(1.4*var(--scale)*var(--header-height) - 30vh, 60vh));
    --scale: 1.4;
    --quad-radius: 0.75rem;
}

/* HOME BUTTON */

.PortfolioLink {
    position: absolute;
    z-index: 1;
    top: 0.75vw;
    left: 3vw;
}

.PortfolioLink:hover {
    transform: scale(1.025, 1.025);
}

#PortfolioLink {
    font-family: 'Chivo', cursive;
    font-size: calc(3vh + 0.5vw);
    font-weight: 800;
    line-height: 1.75vw;
    /* letter-spacing: 0.125vw; */
    text-decoration: underline;
    color: rgba(0, 0, 0, 0.75);
}

/* HEADER */

.PortfolioHeader {
    position: absolute;
    top: 0%;
    height: calc(var(--header-height) + max(4vw, 2.3rem));
    width: 100%;
	background: var(--counter-color-dark); /* linear-gradient(-45deg, #ad41ff, #ff4646, #23a6d5, #23d5ab); */
	background-size: 400% 400%;
	animation: gradient 15s ease infinite;
}
@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.LeftMargin {
    position: absolute;
    top: max(9vw, 4rem);
    height: calc(var(--header-height) - max(8vw, 4rem) - 1vw);
    width: calc(50vw - var(--scale)*var(--height2)/2 - 3*var(--spacing)/2);
    background-color: black;
    opacity: 0.75;
}

.RightMargin {
    position: absolute;
    top: max(9vw, 4rem);
    right: 0;
    height: calc(var(--header-height) - max(8vw, 4rem) - 1vw);
    width: calc(50vw - var(--scale)*var(--height2)/2 - 3*var(--spacing)/2);
    background-color: black;
    opacity: 0.85;
}

.TopMargin {
    position: absolute;
    top: max(9vw, 4rem);
    height: calc(var(--height2)/2 - var(--block-height) - 3*var(--spacing)/2);
    width: 100%;
    background-color: black;
    opacity: 0.85;
}

.BottomMargin {
    position: absolute;
    top: calc(var(--header-height) - calc(var(--height2)/2 - var(--block-height) - 3*var(--spacing)/2)); /*calc(var(--height2) - calc(var(--height2)/2 - var(--block-height) - 3*var(--spacing)/2) + 1vh);*/
    height: calc(var(--height2)/2 - var(--block-height) - 3*var(--spacing)/2);
    width: 100%;
    background-color: black;
    opacity: 0.85;
}

.Blocks {
    position: relative;
    top: calc(var(--height2)/2 - var(--block-height) - 3*var(--spacing)/2 + 0.25vw);
    width: 100vw - max(calc(50vw - var(--scale)*var(--header-height)/2 - 3*var(--spacing)/2), 0vw);
    display: flex;
    flex-direction: column;
}

.Top {
    position: relative;
    top: var(--spacing);
    height: var(--block-height);
    display: flex;
    justify-content: center;
}

.Bottom {
    position: relative;
    top: calc(2*var(--spacing));
    height: var(--block-height);
    display: flex;
    justify-content: center;
}

/* Quadrants */

.Q1 {
     border-top-left-radius: var(--quad-radius);}
.Q2 {
     border-top-right-radius: var(--quad-radius);}
.Q3 {
     border-bottom-left-radius: var(--quad-radius);}
.Q4 {
     border-bottom-right-radius: var(--quad-radius);}

.Q1, .Q2, .Q3, .Q4 {
    position: relative;
    margin-right: calc(var(--spacing)/2);
    margin-left: calc(var(--spacing)/2);
    width: var(--block-width);
    height: var(--block-height);
    background-color: var(--counter-color);
    transition: 0.25s;
    box-shadow: 0 0 0.5vw black;
    font-weight: 600;
    color: white;
    /* border: 0.4vw solid white; */
}

.BlockTop {
    visibility: hidden;
    opacity: 0;
    color: rgb(226, 226, 226);

    position: absolute;
    top: 0;
    width: 100%;
    height: 3vw;
    background-color: rgb(0, 0, 0, 0.8);
    text-align: center;
    transition: 0.5s;
}

.BlockTop#q1 {border-top-left-radius: var(--quad-radius);}
.BlockTop#q2 {border-top-right-radius: var(--quad-radius);}

@import url("https://fonts.googleapis.com/css2?family=Aladin&family=Alfa+Slab+One&family=Luckiest+Guy&family=Monoton&family=Mrs+Sheppards&family=Nanum+Brush+Script&family=Righteous&family=Sedgwick+Ave&display=swap");

#DS-Box-Title {
    position: relative;
    top: -1.75vw;
    font-family: 'Mrs Sheppards', cursive;
    font-size: 2vw;
    font-weight: 500;
    letter-spacing: 0.5vw;
}
#DS-Box-Text {
    visibility: hidden;
    opacity: 0;

    position: relative;
    top: calc(3.5vw - var(--block-height));
    color: black;
    font-size: 0.8vw;
    line-height: 2.5vw;
}

.Q1:hover .BlockTop, .Q2:hover .BlockTop, .Q3:hover .BlockTop, .Q4:hover .BlockTop {
    visibility: visible;
    opacity: 1;
}
.Q1:hover .TitleText1, .Q2:hover .TitleText2, .Q3:hover .TitleText3, .Q4:hover .TitleText4 {
    opacity: 1;
    visibility: hidden;
}
.Q1:hover #DS-Box-Text, .Q2:hover #DS-Box-Text, .Q3:hover #DS-Box-Text, .Q4:hover #DS-Box-Text {
    visibility: visible;
    opacity: 1;
}

.Q1:hover, .Q2:hover, .Q3:hover, .Q4:hover {
    transform: scale(1.025, 1.025);
    transition: 0.5s;
    background-color: rgba(255, 255, 255, 0.4);
}

.TitleText1, .TitleText2, .TitleText3, .TitleText4  {
    position: relative;
    height: 100%; /*calc(var(--block-height) - 2vw);*/
    width: 100%;
    /* top: 1vw;
    left: 1vw; */
    transition: 0.5;
    -webkit-filter: contrast(0) sepia(100%) hue-rotate(190deg) saturate(2000%) brightness(0%);
    filter: contrast(0) sepia(100%) hue-rotate(190deg) saturate(2000%) brightness(0%);
    opacity: 0.85;
}

.TitleText1 {border-top-left-radius: var(--quad-radius);}
.TitleText2 {border-top-right-radius: var(--quad-radius);}
.TitleText3 {border-bottom-left-radius: var(--quad-radius);}
.TitleText4 {border-bottom-right-radius: var(--quad-radius);}

.Image {
    position: absolute;
    z-index: 1;
    top: calc(20vw - var(--iconHeight)/2);
    left: calc(49.7vw - var(--iconHeight)/2);
    height: var(--iconHeight);
    width: var(--iconHeight);
    background-image: var(--image);
    border: 5px solid black;
    border-radius: 100%;
}

.Show {
    position: relative;
    /* filter: blur(5px); */
    /* height: calc(var(--block-height) - 2vw); */
    width: 80%;
    /* bottom: 0px; */
    left: 10%;
    transition: 0.5;
}

/* Body */
@import url("https://fonts.googleapis.com/css2?family=Alfa+Slab+One&family=Monoton&family=Righteous&display=swap");

h1#Portfolio {
    width: 100%;
    background-color: var(--counter-color);
    font-family: 'Monoton', cursive;
    position: relative;
    padding-top: 1vw;
    font-size: max(6vw, 3rem);
    font-weight: 500;
    color: rgba(0, 0, 0, 0.95);
    line-height: max(8vw, 4rem);
    box-shadow: 0px 4px 2px black;
    text-shadow: 0px -0.125vw 0.125vw white;
}

.PortfolioBody {
    margin-left: calc(50vw - var(--portfolio-body-width)/2);
    margin-right: calc(50vw - var(--portfolio-body-width)/2);
    text-align: justify;
}

h2.Section {
    font-family: 'Chivo', cursive;
    font-size: max(min(4vw, 4vh), 1.75rem);
    font-weight: 900;
    margin-top: 8vh;
    padding-top: 0.5vh;
    margin-bottom: 2vh;
    border-top: 0.5vh solid black;
}

div.TBC{
    width: 100%;
    text-align: center;
}

h2.TBC {
    font-family: 'Chivo', cursive;
    font-size: max(min(3vw, 3vh), 1.25rem);
    font-weight: 800;
    margin-top: 3vh;
    margin-bottom: 0%;
    color: rgb(42, 42, 42);
    margin-bottom: 6vh;
    padding-top: 3vh;
    border-top: 0.5vh solid black;
}

h3.Section {
    font-family: 'Chivo', cursive;
    font-size: 2.75vh;
    font-weight: 800;
    margin-bottom: 2vh;
    color: rgb(58, 58, 58);
}

p.Section {
    font-family: "gill sans";
    /* text-shadow: 0 0 1px; */
    font-size: max(min(2.25vw, 2.25vh), 1rem);
    line-height: max(min(3.5vw, 3.5vh), 1.5rem);
    font-weight: 400;
    color: rgb(51, 51, 51);
}

.Project {
    margin-bottom: 8vh;
}

.NLPPosition {
    -ms-transform: translateX(-50%);
    transform: translateX(0%);
}

.NLPPDF {
    margin-left: calc(0.1*var(--portfolio-body-width));
    margin-right: calc(0.1*var(--portfolio-body-width));
    width: calc(0.8*var(--portfolio-body-width));
    height: calc(1.3*0.8*var(--portfolio-body-width));
    /* max-width: 50rem; */
    max-height: 65rem;
}

/* GALLERY */

.image-container {
    width: 100%;
    height: 500px;
    cursor: pointer;
    overflow: hidden;
}

.image-container2 {
    width: 100%;
    height: 375px;
    cursor: pointer;
    overflow: hidden;
}

.Gallery {
    height: calc(var(--portfolio-body-width)/2);
    width: var(--portfolio-body-width);
}

.Gallery2 {
    height: calc(3*var(--portfolio-body-width)/8);
    width: var(--portfolio-body-width);
}

.TEDVideo {
    margin-left: 3%;
    width: 94%;
    height: calc(0.56*var(--portfolio-body-width)*0.94)
}

.FillerPort {
    border-top: 0.5vw solid rgb(0, 0, 0, 0.85);
    width: 100%;
    position: relative;
    text-align: center;
    border: min(0.25vw, 2px) solid rgb(0, 0, 0, 0.85);
    /* border-right: 2px solid rgb(0, 0, 0, 0.85); */
    /* background-color: rgba(50, 50, 50, 0.7); */
    background: linear-gradient(rgba(200, 200, 200, 0.5), rgba(200, 200, 200, 0.25));
}
.FillerPort:before,
.FillerPort:after {
  position: absolute;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
/* Line before hover */
.FillerPort:before {
  top: 0%;
  display: block;
  height: 110%;
  width: 100%;
  content: "";
  background-color: rgb(255, 255, 255, 0);
}

.FillerPort:hover:before,
.current .Filler:before {
    top: 0%;
    opacity: 1;
    height: 110%;
    background-color: rgb(255, 255, 255, 0.25);
}

.TabsPort {
    margin-top: var(--header-height);
    width: 100%;
    display: flex;
    overflow: hidden;
    height: max(4vw, 2.3rem);
}

@import url("https://fonts.googleapis.com/css2?family=Aladin&family=Bowlby+One&family=Chivo:wght@300;400;700;900&family=Erica+One&family=Luckiest+Guy&family=Monoton&family=Mrs+Sheppards&family=Nanum+Brush+Script&family=Permanent+Marker&family=Righteous&family=Sedgwick+Ave&display=swap");

